<template>
  <div class="row">
    <div class="col s12 s12 l6 push-l3">
      <p class="center"><b>Formulário de Cadastro</b></p>
      <form @submit.prevent="salvar()">
        <div class="input-field col s12">
          <i class="material-icons prefix">chevron_right</i>
          <input
            type="text"
            v-model="dados.placa"
            name="placa"
            id="placa"
            required
          />
          <label for="placa">Placa</label>
        </div>
        <div class="input-field col s12">
          <i class="material-icons prefix">chevron_right</i>
          <input
            type="text"
            v-model="dados.chassi"
            name="chassi"
            id="chassi"
            required
          />
          <label for="chassi">Chassi</label>
        </div>
        <div class="input-field col s12">
          <i class="material-icons prefix">chevron_right</i>
          <input
            type="text"
            v-model="dados.renavam"
            name="renavam"
            id="renavam"
            required
          />
          <label for="renavam">Renavam</label>
        </div>
        <div class="input-field col s12">
          <i class="material-icons prefix">chevron_right</i>
          <input
            type="text"
            v-model="dados.modelo"
            name="modelo"
            id="modelo"
            required
          />
          <label for="modelo">Modelo</label>
        </div>
        <div class="input-field col s12">
          <i class="material-icons prefix">chevron_right</i>
          <input
            type="text"
            v-model="dados.marca"
            name="marca"
            id="marca"
            required
          />
          <label for="marca">Marca</label>
        </div>
        <div class="input-field col s12">
          <i class="material-icons prefix">chevron_right</i>
          <input type="text" v-model="dados.ano" name="ano" id="ano" required />
          <label for="ano">Ano</label>
        </div>
        <div class="col s12">
          <button type="submit" class="btn">
            salvar <i class="material-icons right">send</i>
          </button>
        </div>
      </form>
    </div>
  </div>
</template>

<script>
export default {
  name: "Cadastro",
  data() {
    return {
      dados: {
        placa: "",
        chassi: "",
        renavam: "",
        modelo: "",
        marca: "",
        ano: "",
      },
    };
  },
  methods: {
    salvar() {
      
      fetch("http://localhost:3000", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify(this.dados),
      })
        .then((res) => res.json())
        .then((data) => {
          if (data.msg) {
            alert("Salvo com Sucesso !:-)");
            this.dados = {
              placa: "",
              chassi: "",
              renavam: "",
              modelo: "",
              marca: "",
              ano: "",
            };
            this.$router.push({name:'Veiculos'})
            
          } else {
            alert("Desculpe Houve um Erro :-(");
          }
        })
        .catch(err => {
          alert("Desculpe Houve um Erro :-(");
          console.log(err);
        })
    },
  },
};
</script>

